[data-aos="trans-anim1"] {
  transform: translateX(0);
  opacity: 0;
  transition-property: transform, opacity;
}

@media (max-width: 575px) {
  [data-aos="trans-anim1"] {
    transform: translate(0, 30px);
  }
}

