  [data-aos="trans-anim1"].aos-animate {
    transform: translateX(-60px);
  }
}

@media (max-width: 575px) {
  [data-aos="trans-anim1"].aos-animate {
    transform: translate(0, 0);
  }
}

[data-aos="trans-anim2"] {
  transform: translateX(0);
  opacity: 0;
  transition-property: transform, opacity;
}

